-->
<template>
    <view class="give_away_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">赠送</block></cu-custom>
        <form @submit="formSubmit">
            <view class="flex justify-center align-start input_value_box">
                <view class="input_value_title">赠送积分额</view>
                <view class="flex justify-start align-center input_box">
                    <input class="withdraw_money" type="digit" placeholder="输入积分" name="points" />
                </view>
            </view>
            <view class="cu-form-group inputBox">
				<view class="title">对方推广码</view>
				<input placeholder="输入对方推广码" name="user_promo_code"/>
			</view>
            <view class="cu-form-group">
				<view class="title">电话号码</view>
				<input placeholder="输入手机号" name="mobile" value=""/>
			</view>
            <view class="upload_box bg-white">
                <view class="cu-bar">
                    <view class="action title">
                        上传收据
                    </view>
                </view>
                <view class="cu-form-group">
                    <view class="grid col-4 grid-square flex-sub">
                        <view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
                        <image :src="imgList[index]" mode="aspectFill"></image>
                            <view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
                                <text class='cuIcon-close'></text>
                            </view>
                        </view>
                        <view class="solids" @tap="ChooseImage" v-if="imgList.length<1">
                            <text class='cuIcon-cameraadd'></text>
                        </view>
                    </view>
                </view>
            </view>
            <!-- 支付按钮 -->
            <view class="flex justify-center align-center btn_box">
                <button class="cu-btn recharge_btn" form-type="submit" @tap="formSubmit" :disabled="buttonType">赠送</button>
            </view>
        </form>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getGoodsOrderDetail_api,upload_image_api,giftPointsTransfer_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            imgList: [],// 收据
            voucher_pic_id: '',// 收据id
            buttonType: false,
        }
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**选择图片
         * @description: 
         * @param {*}
         * @return {*}
         */
        ChooseImage() {
            uni.chooseImage({
                count: 4, //默认9
                sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album'], //从相册选择
                success: (res) => {
                    if (this.imgList.length != 0) {
                        this.imgList = this.imgList.concat(res.tempFilePaths)
                    } else {
                        this.imgList = res.tempFilePaths
                    }
                    let params = res.tempFilePaths[0];
                    upload_image_api(params,that).then(res=>{
                        that.voucher_pic_id = res.data.file_id;
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            });
        },
        ViewImage(e) {
            uni.previewImage({
                urls: this.imgList,
                current: e.currentTarget.dataset.url
            });
        },
        DelImg(e) {
            uni.showModal({
                title: '提示',
                content: '确定要删除图片吗？',
                cancelText: '取消',
                confirmText: '确定',
                success: res => {
                    if (res.confirm) {
                        that.imgList.splice(e.currentTarget.dataset.index, 1)
                    }
                }
            })
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
            //定义表单规则
            that.buttonType = true;
            let rule = [
                {name:"points", checkType : "notnull", checkRule:"",  errorMsg:"请输入增送的数量"},
                {name:"user_promo_code", checkType : "notnull", checkRule:"",  errorMsg:"请输入对方的对方的推广码"},
                {name:"mobile", checkType : "notnull", checkRule:"",  errorMsg:"请输入对方手机号码"},
                {name:"voucher_pic_id", checkType : "notnull", checkRule:"",  errorMsg:"请上传交易图片"},
            ];
            //进行表单检查
            let formData = e.detail.value;
            formData.voucher_pic_id = that.voucher_pic_id;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                that.buttonType = false;
                return false;
            }
            that.modal({name:'popLoading',value:true});
            that.buttonType = true;
            giftPointsTransfer_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value: {type:true,tipTitle:'提示',tipContent:'赠送成功',linkUrl:1}});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.buttonType = false;
            })
        },
        // 页面生命周期
        onLoad: function (options) {
            that = this;
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>